// Chinese (China) [zh-cn]
import dayjs from '../index';
var locale = {
  name: 'zh-cn',
  weekdays: '______'.split('_'),
  weekdaysShort: '______'.split('_'),
  weekdaysMin: '______'.split('_'),
  months: '___________'.split('_'),
  monthsShort: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'),
  ordinal: function ordinal(number, period) {
    switch (period) {
      case 'W':
        return number + "\u5468";

      default:
        return number + "\u65E5";
    }
  },
  weekStart: 1,
  yearStart: 4,
  formats: {
    LT: 'HH:mm',
    LTS: 'HH:mm:ss',
    L: 'YYYY/MM/DD',
    LL: 'YYYYMD',
    LLL: 'YYYYMDAhmm',
    LLLL: 'YYYYMDddddAhmm',
    l: 'YYYY/M/D',
    ll: 'YYYYMD',
    lll: 'YYYYMD HH:mm',
    llll: 'YYYYMDdddd HH:mm'
  },
  relativeTime: {
    future: '%s',
    past: '%s',
    s: '',
    m: '1 ',
    mm: '%d ',
    h: '1 ',
    hh: '%d ',
    d: '1 ',
    dd: '%d ',
    M: '1 ',
    MM: '%d ',
    y: '1 ',
    yy: '%d '
  },
  meridiem: function meridiem(hour, minute) {
    var hm = hour * 100 + minute;

    if (hm < 600) {
      return '';
    } else if (hm < 900) {
      return '';
    } else if (hm < 1100) {
      return '';
    } else if (hm < 1300) {
      return '';
    } else if (hm < 1800) {
      return '';
    }

    return '';
  }
};
dayjs.locale(locale, null, true);
export default locale;